"use client"

import type React from "react"
import { useState, useEffect, useCallback, useMemo } from "react"
import { useSearchParams } from "next/navigation"
import { Card, CardHeader, CardContent, CardTitle, CardDescription, CardFooter } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { FileText, FileIcon, Download, RefreshCw, Eye, AlertCircle, Upload, Filter, Search } from "lucide-react"
import { Alert, AlertDescription } from "@/components/ui/alert"
import { Badge } from "@/components/ui/badge"
import { Input } from "@/components/ui/input"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { ScrollArea } from "@/components/ui/scroll-area"
import { Skeleton } from "@/components/ui/skeleton"
import { cn } from "@/lib/utils"

interface SubmittedDocument {
  idPFE: number
  idLivrable: 1 | 2
  fichier: string
  nomGroupe: string
  dateSubmission?: string // Added date field
}

interface LivrableNames {
  [key: number]: string
}

export default function TuteurGroupDocumentsPage() {
  const searchParams = useSearchParams()
  const idTuteur = useMemo(() => searchParams.get("idTuteur") || "10", [searchParams])

  const [submittedDocuments, setSubmittedDocuments] = useState<SubmittedDocument[]>([])
  const [isLoading, setIsLoading] = useState(true)
  const [fetchError, setFetchError] = useState<string | null>(null)
  const [searchQuery, setSearchQuery] = useState("")
  const [filterType, setFilterType] = useState<string>("all")
  const [isRefreshing, setIsRefreshing] = useState(false)

  const livrableNames: LivrableNames = {
    1: "Document PDF",
    2: "Présentation",
  }

  const livrableIcons: Record<number, React.ReactNode> = {
    1: <FileText className="h-4 w-4" />,
    2: <FileIcon className="h-4 w-4" />,
  }

  // Mock data for demonstration - remove in production
  const mockDocuments: SubmittedDocument[] = [
    {
      idPFE: 101,
      idLivrable: 1,
      fichier: "/uploads/group1-document.pdf",
      nomGroupe: "Groupe Alpha",
      dateSubmission: "2023-11-15T14:30:00",
    },
    {
      idPFE: 102,
      idLivrable: 2,
      fichier: "/uploads/group1-presentation.pptx",
      nomGroupe: "Groupe Alpha",
      dateSubmission: "2023-11-16T09:45:00",
    },
    {
      idPFE: 103,
      idLivrable: 1,
      fichier: "/uploads/group2-document.pdf",
      nomGroupe: "Groupe Beta",
      dateSubmission: "2023-11-14T16:20:00",
    },
    {
      idPFE: 104,
      idLivrable: 2,
      fichier: "/uploads/group2-presentation.pptx",
      nomGroupe: "Groupe Beta",
      dateSubmission: "2023-11-17T11:10:00",
    },
    {
      idPFE: 105,
      idLivrable: 1,
      fichier: "/uploads/group3-document.pdf",
      nomGroupe: "Groupe Gamma",
      dateSubmission: "2023-11-13T10:05:00",
    },
  ]

  const fetchDocuments = useCallback(async () => {
    setIsLoading(true)
    setFetchError(null)
    setIsRefreshing(true)

    try {
      // In a real environment, uncomment this code and remove the mock data
      /*
      const response = await fetch(
        `http://localhost:5000/api/tut_soumettre/group-documents?idTuteur=${idTuteur}`
      );
      if (response.ok) {
        const data = await response.json();
        setSubmittedDocuments(data);
      } else {
        setFetchError("Impossible de récupérer les documents du groupe");
      }
      */

      // Using mock data for demonstration
      setTimeout(() => {
        setSubmittedDocuments(mockDocuments)
        setIsRefreshing(false)
        setIsLoading(false)
      }, 800)
    } catch (error) {
      setFetchError("Erreur lors de la récupération des documents")
      console.error("Fetch error:", error)
      setIsRefreshing(false)
      setIsLoading(false)
    }
  }, [idTuteur])

  useEffect(() => {
    fetchDocuments()
  }, [fetchDocuments])

  // Filter and search documents
  const filteredDocuments = useMemo(() => {
    return submittedDocuments.filter((doc) => {
      const matchesSearch = doc.nomGroupe.toLowerCase().includes(searchQuery.toLowerCase())
      const matchesFilter = filterType === "all" || doc.idLivrable.toString() === filterType
      return matchesSearch && matchesFilter
    })
  }, [submittedDocuments, searchQuery, filterType])

  // Format date for display
  const formatDate = (dateString?: string) => {
    if (!dateString) return "Date inconnue"
    const date = new Date(dateString)
    return new Intl.DateTimeFormat("fr-FR", {
      day: "2-digit",
      month: "2-digit",
      year: "numeric",
      hour: "2-digit",
      minute: "2-digit",
    }).format(date)
  }

  const handleDownload = (fileUrl: string) => {
    // In production, use the actual server URL
    const fullUrl = `http://localhost:5000${fileUrl}`
    window.open(fullUrl, "_blank")
  }

  const handlePreview = (fileUrl: string) => {
    // In production, use the actual server URL
    const fullUrl = `http://localhost:5000${fileUrl}`
    window.open(fullUrl, "_blank")
  }

  return (
    <div className="container mx-auto py-8 px-4">
      <Card className="w-full max-w-4xl mx-auto shadow-lg border border-border/10 overflow-hidden bg-gradient-to-b from-background to-muted/20 rounded-xl">
        <CardHeader className="pb-6 border-b border-border/40 bg-muted/30">
          <div className="flex flex-col md:flex-row md:items-center md:justify-between gap-4">
            <div>
              <CardTitle className="text-2xl font-bold bg-clip-text text-transparent bg-gradient-to-r from-primary to-primary/80">
                Documents des Groupes Supervisés
              </CardTitle>
              <CardDescription className="text-muted-foreground mt-1">
                Consultez les documents soumis par les groupes que vous encadrez
              </CardDescription>
            </div>
            <Button
              variant="outline"
              size="sm"
              onClick={fetchDocuments}
              disabled={isRefreshing}
              className="h-9 hover:bg-primary/5 hover:text-primary self-start md:self-center"
            >
              <RefreshCw className={`h-3.5 w-3.5 mr-1.5 ${isRefreshing ? "animate-spin" : ""}`} />
              Actualiser
            </Button>
          </div>
        </CardHeader>

        <CardContent className="p-6 space-y-6">
          {/* Search and filter controls */}
          <div className="flex flex-col sm:flex-row gap-3">
            <div className="relative flex-1">
              <Search className="absolute left-2.5 top-2.5 h-4 w-4 text-muted-foreground" />
              <Input
                placeholder="Rechercher par nom de groupe..."
                className="pl-9 bg-background/50"
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
              />
            </div>
            <div className="flex items-center gap-2">
              <Filter className="h-4 w-4 text-muted-foreground" />
              <Select value={filterType} onValueChange={setFilterType}>
                <SelectTrigger className="w-[180px] bg-background/50">
                  <SelectValue placeholder="Type de document" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="all">Tous les types</SelectItem>
                  <SelectItem value="1">Document PDF</SelectItem>
                  <SelectItem value="2">Présentation</SelectItem>
                </SelectContent>
              </Select>
            </div>
          </div>

          {/* Document display area */}
          <div className="mt-4">
            {isLoading && !isRefreshing ? (
              <div className="space-y-3">
                {[1, 2, 3].map((i) => (
                  <div key={i} className="flex items-center p-4 rounded-xl border border-border/40">
                    <Skeleton className="h-10 w-10 rounded-full mr-3" />
                    <div className="space-y-2 flex-1">
                      <Skeleton className="h-4 w-40" />
                      <Skeleton className="h-3 w-24" />
                    </div>
                    <div className="flex gap-2">
                      <Skeleton className="h-9 w-28" />
                      <Skeleton className="h-9 w-28" />
                    </div>
                  </div>
                ))}
              </div>
            ) : fetchError ? (
              <Alert
                variant="destructive"
                className="bg-destructive/10 text-destructive border border-destructive/20 rounded-lg"
              >
                <AlertCircle className="h-4 w-4 mr-2" />
                <AlertDescription>{fetchError}</AlertDescription>
                <Button variant="link" onClick={fetchDocuments} className="text-destructive ml-2">
                  Réessayer
                </Button>
              </Alert>
            ) : filteredDocuments.length > 0 ? (
              <ScrollArea className="h-[400px] rounded-md border border-border/40 p-2">
                <div className="space-y-3 p-2">
                  {filteredDocuments.map((doc) => (
                    <div
                      key={`${doc.idPFE}-${doc.idLivrable}`}
                      className="flex flex-col sm:flex-row sm:items-center justify-between p-4 bg-card rounded-xl border border-border/40 hover:shadow-md transition-all duration-300 hover:border-primary/20 gap-4"
                    >
                      <div className="flex items-center gap-3">
                        <div
                          className={cn(
                            "p-3 rounded-full",
                            doc.idLivrable === 1 ? "bg-blue-500/10" : "bg-amber-500/10",
                          )}
                        >
                          {livrableIcons[doc.idLivrable] ?? (
                            <FileText
                              className={cn("h-4 w-4", doc.idLivrable === 1 ? "text-blue-500" : "text-amber-500")}
                            />
                          )}
                        </div>
                        <div className="space-y-1">
                          <div className="flex flex-wrap items-center gap-2">
                            <span className="font-medium">{livrableNames[doc.idLivrable]}</span>
                            <Badge variant="secondary" className="font-normal">
                              {doc.nomGroupe}
                            </Badge>
                          </div>
                          <p className="text-xs text-muted-foreground">Soumis le {formatDate(doc.dateSubmission)}</p>
                        </div>
                      </div>
                      <div className="flex gap-2 self-end sm:self-center">
                        <Button
                          variant="outline"
                          size="sm"
                          className="hover:bg-primary/5 hover:text-primary transition-colors"
                          onClick={() => handleDownload(doc.fichier)}
                        >
                          <Download className="h-4 w-4 mr-2" />
                          Télécharger
                        </Button>
                        <Button
                          variant="outline"
                          size="sm"
                          className="hover:bg-primary/5 hover:text-primary transition-colors"
                          onClick={() => handlePreview(doc.fichier)}
                        >
                          <Eye className="h-4 w-4 mr-2" />
                          Prévisualiser
                        </Button>
                      </div>
                    </div>
                  ))}
                </div>
              </ScrollArea>
            ) : (
              <div className="flex flex-col items-center justify-center p-8 bg-muted/10 rounded-xl border border-border/40">
                <Upload className="h-12 w-12 text-muted-foreground mb-3 opacity-50" />
                <p className="text-muted-foreground text-center font-medium">Aucun document trouvé</p>
                <p className="text-muted-foreground text-center text-sm mt-1">
                  {searchQuery || filterType !== "all"
                    ? "Essayez de modifier vos critères de recherche"
                    : "Aucun document soumis par vos groupes pour le moment"}
                </p>
              </div>
            )}
          </div>
        </CardContent>

        <CardFooter className="flex justify-between p-6 pt-0">
          <p className="text-xs text-muted-foreground">{filteredDocuments.length} document(s) trouvé(s)</p>
        </CardFooter>
      </Card>
    </div>
  )
}

